
import Vue from 'vue';

import { mapActions, mapGetters } from 'vuex';

export default Vue.extend({
  name: 'CategoriesCreateComponent',

  data: () => ({
    valid: false,
    loading: true,

    title: null,
    titleRules: [(v: string) => !!v || 'Это поле нужно заполнить'],

    limit: null,
    limitRules: [
      (v: string) => !!v || 'Это поле нужно заполнить',
      (v: string) =>
        /^\d+(?:[\.,]\d+)?$/.test(v) ||
        'Это поле должно содержать только числа',
    ],
  }),

  async mounted() {},

  methods: {
    ...mapActions(['createCategoryAction']),

    async createCategory() {
      await this.$store.dispatch('createCategoryAction', {
        title: this.title,
        limit: this.limit,
      });

      this.title = null;
      this.limit = null;

      const form: any = this.$refs.form;
      form.reset();
    },
  },
});
